import React from 'react';
import styled from 'styled-components';
import Savio from '../../images/savio.png';
import {Link} from 'react-router-dom';
import img from '../../images/bc1.jpg';

const SectionAboutMe = () => {
  return (
    <SectionAboutMeWrapper img={img}>
      <section className="main" id="aboutme">

        <h1 className="texto">Sobre Mim</h1>

        <div className="container-fluid">

          <div className="foto-container">
            <img className="foto" src={Savio} />
            <p className="nome">
              Everton <span><b>Sávio</b></span> Santos Lucas
            </p>
          </div>
          <div className="aboutme">
            <h4> Quem sou eu:</h4>
            <p>
              Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo.
              Sed ut perspiciatis unde omnis iste natus error sit volupt
            </p>
            <p>
              Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo.
              Sed ut perspiciatis unde omnis iste natus error sit volupt
            </p>
          </div>

          <div className="aboutme">
            <h4> Tecnologias</h4>
            <p>
              Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo.
              Sed ut perspiciatis unde omnis iste natus error sit volupt
            </p>

          </div>

        </div>
        <div className="botao-container">
          <a href="#portfolio" className="main-link">Conheça meu Trabalho</a>
        </div>

      </section>
    </SectionAboutMeWrapper>
  );
};

export default SectionAboutMe;

const SectionAboutMeWrapper = styled.div`

.nome{
  background-color: whitesmoke;
  border-radius: 2%;
  padding-left: 2.5vh;
  padding-right: 2.5vh;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
}
.container-fluid{
  background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.3)), url(${props => props.img});
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding:25px;
}
.texto{
  padding: 5vh;
  text-align: center;
  margin-bottom: 5vh;
}

.botao-container{
  text-align: center;

  padding: 10vh;
}

.foto-container{
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center;
  padding-bottom: 2.5vh;
}

.foto{
  border: 2px solid whitesmoke;
  border-radius: 50%;
  width: 30vh;
  max-height: 30vh;
  margin-top: 2.5vh;
  margin-bottom: 2.5vh;
  margin-left: 5vh;
  margin-right: 5vh;
  
}



.aboutme{
  min-width: 400px;
  max-width: 20vw;
  text-align: justify;
  padding: 2vh;
  margin: 1vh;
  font-size: 2vh;
  background-color: whitesmoke;
  border-radius: 1%;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
}



.main-link {

  padding: 0.5rem 1rem;
  background: rgba(0,0,0,0.5);
  border-radius: 5px;
  color: white;
  outline: 1.5px solid black;
  outline-offset: 6px;
  border: 1px solid rgba(0,0,0,0.6);
  transition: 1s !important;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;


}
.main-link:hover {
  background: #282c34;
  color: white;
  outline: 1px solid #282c34;
  text-decoration: none;
  border: 1px solid #282c34;
}

  #aboutme {
  border-radius: 5px;
  min-height: 100vh;
  margin-top: 0.5vh;
  padding: 0;
  background: whitesmoke;
  
  color: rgb(50,50,50);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 112.5%;
  line-height: 1.6em;
  }
`;
