import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import {SocialData, HomeIcon} from '../context/SocialData';
import Menu from './Menu';
//yarn add styled-components

const NavBar = () => {
  const [size, setSize] = useState([window.innerWidth, window.innerHeight]);
  const [width, height] = [...size];

  useEffect(() => {
    const handleResize = () => {
      setSize([window.innerWidth, window.innerHeight]);
    };
    window.addEventListener('resize', handleResize);
    /////BOA PRATICA, LIMPAR EVENTO COM RETURN//
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });
  if (width >= 1200 && height > 650) {
    return (
      <NavWrapper>
        <div className="nav-rotate">
          <div className="icons">
            {SocialData.map((item) => (
              <a
                className="iconJS"
                href={item.url}
                target="blank"
                key={item.id}
              >
                {item.icon}
              </a>
            ))}
          </div>
          <div>Contato</div>
          <div>Sobre Mim</div>
          <div>Projetos</div>
          <div>{/* LAYOUT ESPACO */}</div>
          <div className="iconHome">
            <a className="iconJS" href={HomeIcon.url}>
              {HomeIcon.icon}
            </a>
          </div>
        </div>
      </NavWrapper>
    );
  } else {
    return <Menu />;
  }
};

const NavWrapper = styled.nav`
  .nav-rotate {
    text-transform: uppercase;
    color: black;
    width: 100vh;
    height: 7.8vw;
    font-size: 1.8vh;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: whitesmoke;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: space-around;
    -webkit-transform-origin: left top;
    -webkit-transform: rotate(-90deg) translateX(-100%);
  }
  .icons {
    width: 22vh;
    font-size: 2.5vh;
    display: flex;
    justify-content: space-evenly;
    margin-left: 5vh;
  }
  .iconJS {
    color: black;
    -webkit-transform-origin: left top;
    -webkit-transform: rotate(-270deg) translateX(25%);
    transition: 0.5s;
    &:hover {
      color: #61dafb;
    }
  }
  .iconHome {
    font-size: 3vh;
    display: flex;
  }
`;

export default NavBar;
