import React from 'react';
import styled from 'styled-components';
import mainBcg from '../../images/pc3.jpg';
import IconDown from './IconDown';
import Typical from 'react-typical';

export default function Projects({img = mainBcg}) {
  return (
    <ProjectsWrapper img={img}>
      <main id="home" className="container-fluid">
        <h1 className="lg-heading">
          Everton <span className="text-secondary">Sávio</span>
        </h1>
        <h1 className="sm-heading">
          <Typical
            steps={[
              '#',
              2000,
              '#Full',
              500,
              '#Full-',
              1000,
              '#Full-Stack',
              2000,
              '#Full',
              500,
              '#',
              1000,
              '#Data',
              1000,
              '#Data ',
              50,
              '#Data Scientist',
              2000,
            ]}
            loop={1}
            wrapper="p"
          />
          {/* Data Scientist Full-Stack */}
        </h1>
        <div className="icone">
          <IconDown />
        </div>
      </main>
    </ProjectsWrapper>
  );
}

const ProjectsWrapper = styled.div`
  background: linear-gradient(var(--primaryRGBA), var(--primaryRGBA)),
    url(${props => props.img}) center/ cover no-repeat;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
  border-radius: 5px;
  border-radius: 5px;

  .icone {
    text-align:center;
  }
  h1,
  h2,
  h3 {
    margin: 0;
    font-weight: 400;
    color: #fff;
    &.lg-heading {
      font-size: 10vh;
      padding-bottom: 1rem;
    }

    &.sm-heading {
      padding: 1rem 0rem 0.5rem 0.5rem;
      font-size: 4vh;
      border-radius: 5px;
      background-color: #282c34;
      letter-spacing: 0.2ch;
      font-weight: 400;
      opacity: 0.8;
      border: 1px solid whitesmoke;
    }
  }

  .text-secondary {
    color: #eece1a !important;
  }

  main {
    min-height: 100vh;
    padding-top: 20vh;
  }
`;
