import React, {useRef} from 'react';
import styled from 'styled-components';
import {disablePageScroll, enablePageScroll} from 'scroll-lock';
import {SocialData} from '../context/SocialData';
//yarn add scroll-lock

const Menu = () => {
  let checkMe = useRef ();
  const $scrollableElement = document.querySelector ('.my-scrollable-element');
  //Pass the element to the argument and disable scrolling on the page

  const handleToggle = event => {
    //console.log(checkMe.current.checked);
    checkMe.current.checked === true
      ? disablePageScroll ($scrollableElement)
      : enablePageScroll ($scrollableElement);
  };

  const handleMenu = event => {
    event.target.type === 'checkbox'
      ? disablePageScroll ($scrollableElement)
      : enablePageScroll ($scrollableElement);
    checkMe.current.checked = false;
  };

  return (
    <MenuWrapper>
      <div className="menu-container">
        <input
          type="checkbox"
          ref={checkMe}
          onChange={handleToggle}
          className="toggler"
        />
        <div className="hamburguer">
          <div />
        </div>
        <div className="menu" onClick={handleMenu}>
          <div>
            <ul>
              <li>
                <a href="#home" onClick={handleMenu}>
                  Inicio
                </a>
              </li>
              <li>
                <a href="#portfolio" onClick={handleMenu}>
                  Projetos
                </a>
              </li>
              <li>
                <a href="#aboutme" onClick={handleMenu}>
                  Sobre Mim
                </a>
              </li>
              <li>
                <a href="#contactme" onClick={handleMenu}>
                  Contato
                </a>
              </li>
            </ul>

            <div className="menu-footer">
              {SocialData.slice (0).reverse ().map (item => (
                <a href={item.url} target="blank" key={item.id}>
                  {item.icon}
                </a>
              ))}
            </div>
          </div>
        </div>
      </div>
    </MenuWrapper>
  );
};

const MenuWrapper = styled.nav`
  .menu-footer {
    position: absolute;
    bottom: 10vh;
  }

  .menu-footer > a {
    font-size: 25px;
    margin: 5vh;
    transition: 1s;
    color: whitesmoke;
    &:hover {
      color: #61dafb;
    }
  }

  .menu-container {
    position: fixed;
    margin-top: 20px;
    margin-right: 20px;
    top: 0;
    right: 0;
    z-index: 2;
  }
  .toggler {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    opacity: 0;
    z-index: 3;
    cursor: pointer;
  }
  .hamburguer {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    width: 60px;
    height: 60px;
    border-radius: 20%;
    background-color: #282c34;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 3px 3px 3px 0px rgba(0, 0, 0, 0.75);
  }

  .hamburguer > div {
    position: absolute;
    width: 50%;
    height: 3px;
    border-radius: 1.5px;
    background-color: whitesmoke; //#61dafb;
    transition: 0.4s;
  }

  .hamburguer > div:before,
  .hamburguer > div:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 3px;
    border-radius: 1.5px;
    background-color: whitesmoke; //#61dafb;
  }
  .hamburguer > div:before {
    top: -10px;
  }

  .hamburguer > div:after {
    top: 10px;
  }

  .toggler:checked + .hamburguer > div {
    transform: rotate(225deg);
  }

  .toggler:checked:hover + .hamburguer > div {
    transform: rotate(315deg);
  }

  .toggler:checked + .hamburguer > div:after,
  .toggler:checked + .hamburguer > div:before {
    transform: rotate(90deg);
    top: 0;
  }

  .toggler:checked ~ .menu {
    visibility: visible;
  }

  .toggler:checked ~ .menu > div {
    transform: translateX(-50%) translateY(-50%);
    opacity: 1;
  }

  .menu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    visibility: hidden;
  }
  .menu > div {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translateX(-150%) translateY(-50%);
    background-color: rgba(0, 0, 0, 0.95);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: 0.6s;
    opacity: 0;
  }

  .menu > div > ul > li {
    list-style: none;
    text-decoration: none;
    margin-right: 2.25rem;
  }

  .menu > div > ul > li > a {
    text-decoration: none;
    margin-bottom: 2rem;
    display: inline-block;
    font-size: 1.2rem;
    font-weight: 550;
    text-transform: uppercase;
    color: whitesmoke;
    transition: 0.6s;
    &:hover {
      color: #61dafb;
    }
  }
`;

export default Menu;
